@import url("https://fonts.googleapis.com/css2?family=Sriracha&display=swap");
.project-section {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 150vh;
  overflow: auto;
  width: 100%;
}
.spacer {
  background-color: rgb(255, 246, 167);
  width: 70%;
  height: 4px;

  border-radius: 5px;

  margin: 40px;
}
.project-title {
  color: white;
  font-size: 50px;
  font-weight: bold;
  font-family: Sriracha;

  margin: 15px;
  margin-bottom: 30px;
}

.project-row {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  width: 100%;
  height: 100%;

  overflow: auto;
}

.project-box {
  border: 2px solid gray;
  min-width: 30%;
  width: 35%;
  max-height: 120vh;
  overflow: auto;
}

.project-name {
  color: white;
  font-size: 32px;
  font-family: Sriracha;
  margin: 10px;
  text-align: center;
  border-bottom: 2px solid gray;
}

.project-footer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  border-top: 2px solid gray;

  overflow: auto;
}

.repo-link {
  color: white;
  font-size: 16px;
  font-family: Sriracha;
  margin: 10px;
  margin-right: 30px;
  cursor: pointer;
}

.author-text {
  color: white;
  margin: 10px;
  font-size: 16px;
  font-family: Sriracha;
}

.project-content {
  color: white;

  font-size: 18px;
  font-family: Sriracha;
  text-align: center;
  margin: 5px;

  padding-bottom: 15px;
  border-bottom: 2px solid gray;
}

.subtitle-text {
  color: white;

  font-size: 22px;
  font-family: Sriracha;
  text-align: center;
  margin: 10px;
}

.github-icon {
  margin-right: 5px;
}

.difficulty {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  margin: 5px;
  margin-right: 30px;

  overflow: auto;
}

.difficulty-square {
  margin-left: 15px;
  margin-right: 15px;
}

.difficulty-text {
  color: white;
  flex: 1;
  font-size: 20px;
  font-family: Sriracha;
  text-align: left;
  margin-left: 20px;
}

.images-row {
  display: flex;
  justify-content: center;

  overflow: auto;
  align-items: center;
  flex-direction: row;
  height: 20%;
  margin-top: 15px;
}

.project-photo {
  width: 50px;
  height: 50px;
}

.app-link {
  cursor: pointer;
  margin: 30px;
  color: lightblue;
  text-decoration: underline;
}

.authors-names {
  color: white;
  margin: 10px;
  font-size: 16px;
  font-family: Sriracha;
}

.author-box {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;

  overflow: auto;
  width: 60%;
}
